<template>
  <header class="app-header">
    <div class="graph-icon-container">
      <div class="graph-icon"></div>
    </div>
    <nav class="nav-bar-container" v-if="uid.length != 0">
      <router-link class="link-element" to="/">Accounts</router-link>
      |
      <router-link class="link-element" to="/categories">Categories</router-link>
    </nav>
    <div class="logout-icon-container" @click="logoutUser" v-if="uid.length != 0">
      <div class="logout-icon"></div>
    </div>
  </header>
</template>

<script>
import { signOut } from "firebase/auth";
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'Header',
  data() {
    return {
    }
  },
  computed: {
    ...mapGetters(["auth", "uid",]),
  },
  methods: {
    ...mapMutations(["setUid"]),
    logoutUser() {
      signOut(this.auth).then(_ => {
        this.setUid("");
        this.$router.replace({name: "home"});
      })
    }
  },
}
</script>

