<template>
  <div>
    <div class="main-title">
      Bank Accounts
      <BankAccountCreateEdit :docId="0" :name="''" />
    </div>
    <div class="create-account-container" v-if="false">
      <BankAccountCreateEdit :docId="0" :name="''" />
    </div>
    
    <div class="create-account-container">
      <div class="btn-container" @click="$router.replace({name: 'query'})">
        <div class="chart-btn"></div>
        Data Visualization
      </div>
    </div>
    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(bankAccountsList)" :key="k" class="bank-account-element" @click="goToTransactions(k)" data-transaction="true">
        <div class="bank-account-title" data-transaction="true">
          {{ bankAccountsList[k].name }}
          <BankAccountCreateEdit :docId="k" :name="bankAccountsList[k].name" />
        </div>
        <div class="bank-account-balance" data-transaction="true">{{ getBalanceFromBankAccount(k) }}â‚¬</div>
      </div>
    </div>
  </div>
</template>

<script>
import BankAccountCreateEdit from "@/components/BankAccountsCreateEdit.vue"
import { mapGetters, mapMutations } from 'vuex';
import { collection, where, query, getDocs } from "firebase/firestore";

export default {
  name: 'BankAccountsList',
  components: {
    BankAccountCreateEdit,
  },
  data() {
    return {
      bankAccountsList: {},
    }
  },
  computed: {
    ...mapGetters(["db", "appData", "categoriesList",]),
  },
  methods: {
    ...mapMutations(["setBankAccountId", "setAppData", "setCategoriesList"]),
    async getBanksList() {
      const q = query(collection(this.db, "bankAccounts"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.bankAccountsList[doc.id] = doc.data();
        this.getTransactionsList(doc.id);
      });
    },
    async getTransactionsList(bankAccountId) {
      const q = query(collection(this.db, "transactions"), where("bankAccountId", "==", bankAccountId));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        if(this.bankAccountsList[bankAccountId]["transactions"]) {
          this.bankAccountsList[bankAccountId]["transactions"][doc.id] = doc.data();
        } else {
          this.bankAccountsList[bankAccountId]["transactions"] = {};
          this.bankAccountsList[bankAccountId]["transactions"][doc.id] = doc.data();
        }
      });
    },
    goToTransactions(bankAccountId) {
      if(event.target.dataset.transaction == "true") {
        this.setBankAccountId(bankAccountId);
        // this.setAppData(this.bankAccountsList[bankAccountId]);
        this.$router.replace({name: "transactions"});
      }
    },
    async getCategoriesList() {
      const categoriesList = [];
      const q = query(collection(this.db, "categories"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        categoriesList[doc.id] = doc.data();
      });

      this.setCategoriesList(categoriesList);
    },
    getBalanceFromBankAccount(id) {
      if(this.bankAccountsList[id]["transactions"]) {
        return Object.values(this.bankAccountsList[id]["transactions"]).reduce((r, v) => {
          return v.amount + Math.round(r*100)/100;
        }, 0);
      }
      return 0;
    }
  },
  mounted() {
    if(Object.values(this.appData).length == 0) {
      this.getBanksList();
    }
    if(Object.keys(this.categoriesList).length == 0) {
      this.getCategoriesList();
    }
  },
  watch: {
    bankAccountsList: {
      handler(val, oldVal) {
        if(Object.values(val).length != 0 && val != oldVal) this.setAppData(val);
        if(Object.values(val).length == 0) this.bankAccountsList = this.appData;
      },
      immediate: true,
      deep: true,
    }
  }
}
</script>

