<template>
  <div>
    <div class="create-account-container" @click="openModal" v-if="docId == 0 && false">
      <div class="create-account-btn"></div>
      Add new Bank Account
    </div>
    <div class="create-account-btn" @click="openModal" v-else-if="docId == 0"></div>

    <div class="edit-btn" @click="openModal" v-else></div>
    <dialog class="modal-container" :id="docId + '-modal'">
      <div class="close-btn-container">
        <div class="close-btn" @click="closeModal"></div>
      </div>
      <div class="input-container">
        <label for="ba-name">Name:</label>
        <input id="ba-name" type="text" v-model="bankAccountName">
      </div>
      <div class="create-btn" :class="[allowCreation ? '' : 'btn-disabled']" @click="allowCreation ? createEditBankAccount() : ''">{{ docId == 0 ? "Create" : "Save" }}</div>
    </dialog>
  </div>
</template>

<script>
import { doc, setDoc, updateDoc } from "firebase/firestore"; 
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'BankAccountsCreateEdit',
  props: ["docId", "name"],
  data() {
    return {
      bankAccountName: null,
    }
  },
  computed: {
    ...mapGetters(["db", "uid", "appData"]),
    allowCreation() {
      return (this.bankAccountName != null && this.bankAccountName != "" && this.bankAccountName.trim() != "" && this.bankAccountName.trim() == this.bankAccountName);
    }
  },
  methods: {
    ...mapMutations(["setAppData"]),
    openModal() {
      this.bankAccountName = this.name;
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.showModal();
    },
    closeModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.close();
    },
    async createEditBankAccount() {
      if(this.docId == 0) {
        const newId = this.uid + Date.now().toString();
        const newData = {
          name: this.bankAccountName,
          creationDate: Date.now(),
          lastEditionDate: Date.now()
        };
        await setDoc(doc(this.db, "bankAccounts", newId), newData).then(_ => {
          this.appData[newId] = newData;
          this.setAppData(this.appData);
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      } else {
        const bankAccountRef = doc(this.db, "bankAccounts", this.docId);
        const newData = {
          name: this.bankAccountName,
          lastEditionDate: Date.now()
        };
        await updateDoc(bankAccountRef, newData).then(_ => {
          const updatedDataElement = JSON.parse(JSON.stringify(this.appData[this.docId]));
          updatedDataElement.name = newData.name;
          updatedDataElement.lastEditionDate = newData.lastEditionDate;
          this.appData[this.docId] = updatedDataElement;
          this.setAppData(this.appData);
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      }
    },
  },
}
</script>

