<template>
  <div class="login-container" v-if="visible">
    <div class="login-input-container">
      <label for="email">Email:</label>
      <input id="email" type="text" v-model="email">
    </div>
    <div class="login-input-container">
      <label for="password">Password:</label>
      <input id="password" type="password" v-model="password" @keydown.enter="login">
    </div>
    <div class="error-msg" :class="[showErrorMsg ? '' : 'not-visible']">
      Wrong user or password
    </div>
    <div class="login-btn" @click="login">
      Login
    </div>
  </div>
</template>

<script>
import { signInWithEmailAndPassword, onAuthStateChanged } from "firebase/auth";
import { mapGetters, mapMutations } from "vuex";

export default {
  name: 'Login',
  data() {
    return {
      visible: false,
      email: "",
      password: "",
      showErrorMsg: false,
    }
  },
  computed: {
    ...mapGetters(["app", "auth", "uid"]),
  },
  methods: {
    ...mapMutations(["setUid"]),
    login() {
      signInWithEmailAndPassword(this.auth, this.email, this.password)
        .then((userCredential) => {
          // Signed in 
          const user = userCredential.user;
          this.setUid(user.uid);
          // console.log(this.uid);
          // this.$router.replace({name: "categories"});
        })
        .catch((error) => {
          this.showErrorMsg = true;
          const errorCode = error.code;
          const errorMessage = error.message;
        });
    }
  },
  watch: {
    email() {
      this.showErrorMsg = false;
    },
    password() {
      this.showErrorMsg = false;
    }
  },
  mounted() {
    try {
      onAuthStateChanged(this.auth, (user => {
        if(user) {
          this.setUid(user.uid);
          this.visible = true;
        } else {
          this.setUid("");
          this.visible = true;
        }
      }));
    } catch(e) {
      console.log(e);
      this.visible = true;
    }
  },
}
</script>

