import { createStore } from 'vuex'

export default createStore({
  state: {
    app: "",
    auth: "",
    db: "",
    uid: "",
    bankAccountId: "",
    appData: {},
    categoriesList: {},
    query: {},
  },
  getters: {
    app(state) {
      return state.app;
    },
    auth(state) {
      return state.auth;
    },
    db(state) {
      return state.db;
    },
    uid(state) {
      return state.uid;
    },
    bankAccountId(state) {
      return state.bankAccountId;
    },
    appData(state) {
      return state.appData;
    },
    categoriesList(state) {
      return state.categoriesList;
    },
    query(state) {
      return state.query;
    },
  },
  mutations: {
    setApp(state, app) {
      state.app = app;
    },
    setAuth(state, auth) {
      state.auth = auth;
    },
    setDb(state, db) {
      state.db = db;
    },
    setUid(state, uid) {
      state.uid = uid;
    },
    setBankAccountId(state, bankAccountId) {
      state.bankAccountId = bankAccountId;
    },
    setAppData(state, appData) {
      state.appData = appData;
    },
    setCategoriesList(state, categoriesList) {
      state.categoriesList = categoriesList;
    },
    setQuery(state, query) {
      state.query = query;
    },
  },
  actions: {
  },
  modules: {
  }
})
